<template>
  <v-card class="ma-5" elevation="20">
    <v-snackbar v-model="snackbar" :timeout="4000">
      <span class="mx-2">Log has been successfully added!</span>
      <v-btn text color="white" @click="snackbar = false">Close</v-btn>
    </v-snackbar>
    <v-snackbar v-model="editSnackbar" :timeout="4000">
      <span class="mx-2">Log has been successfully updated!</span>
      <v-btn text color="white" @click="editSnackbar = false">Close</v-btn>
    </v-snackbar>
    <v-card-title>
      Internal & External Communication
      <v-spacer></v-spacer>
      <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details></v-text-field>
      <v-flex>
        <Add @logAdded="snackbar = true"/>
      </v-flex>
    </v-card-title>
    <v-data-table :headers="headers" :items="logs" :search="search" sort-by="date">
        <template v-slot:[`item.type`]="{ item }">
          <v-chip :color="getColor(item.type)" dark>{{ item.type }}</v-chip>
        </template>
        <template v-slot:[`item.actions`]="{ item }">
          <v-tooltip top>
            <template v-slot:activator="{ on, attrs }">
              <v-icon class="mr-2" @click="editBtn(item)" v-bind="attrs" v-on="on">mdi-file-edit</v-icon>
            </template>
            <span>Edit</span>
          </v-tooltip>
          <v-tooltip top>
            <template v-slot:activator="{ on, attrs }">
              <v-icon @click="del(item.id)" v-bind="attrs" v-on="on">mdi-delete</v-icon>
            </template>
            <span>Delete</span>
          </v-tooltip>
        </template>
    </v-data-table>

    <!-- edit dialog -->
    <v-row justify="center">
      <v-dialog v-model="dialog" fullscreen hide-overlay trasnsition="dialog-bottom-transition">
        <v-card>
          <v-toolbar dark color="deep-purple darken-3">
            <v-btn icon dark @click="dialog = false">
              <v-icon>mdi-close</v-icon>
            </v-btn>
            <v-toolbar-title>Edit Log</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-toolbar-items>
              <v-btn dark text @click="updateLog()">Update</v-btn>
            </v-toolbar-items>
          </v-toolbar>
          <v-card-text class="mt-4">
                    <v-form ref="form">
                        <v-container>
                            <v-row>
                                <v-col sm="12">
                                    <v-textarea label="Subject" v-model="editLogs.subject" prepend-icon="mdi-folder" outlined height="80" :rules="inputRules"></v-textarea>
                                </v-col>
                                <v-col sm="6">
                                    <v-text-field v-model="editLogs.date" label="Date" persistent-hint prepend-icon="mdi-calendar" outlined :rules="inputRules"></v-text-field>
                                </v-col>
                                <v-col sm="6">
                                    <v-text-field label="Sender" v-model="editLogs.sender" prepend-icon="mdi-account-arrow-right" outlined :rules="inputRules"></v-text-field>
                                </v-col>
                                <v-col sm="7">
                                    <v-text-field label="Control #" v-model="editLogs.control_no" prepend-icon="mdi-numeric-9-box-multiple" outlined :rules="inputRules"></v-text-field>
                                </v-col>
                                <v-col sm="5">
                                    <v-text-field v-model="editLogs.type" label="Transmittal Type" prepend-icon="mdi-clipboard-file" outlined :rules="inputRules"></v-text-field>
                                </v-col>
                                <v-col sm="12">
                                    <v-textarea label="PM's Instruction" v-model="editLogs.pm_instruction" prepend-icon="mdi-file-powerpoint" outlined height="80" :rules="inputRules"></v-textarea>
                                </v-col>
                                <v-col sm="6">
                                    <v-text-field label="Received By" v-model="editLogs.received_by" prepend-icon="mdi-account-box-multiple" outlined :rules="inputRules"></v-text-field>
                                </v-col>
                                <v-col sm="6">
                                    <v-text-field v-model="editLogs.received_date" label="Received Date" hint="MM/DD/YYYY format" persistent-hint prepend-icon="mdi-calendar" outlined :rules="inputRules"></v-text-field>
                                </v-col>
                            </v-row>
                        </v-container>
                    </v-form>
                </v-card-text>
        </v-card>
      </v-dialog>
    </v-row>
  </v-card>
</template>

<script>
import Add from '../components/PopAdd.vue'
import db from '@/fb'

export default {
  components: { Add },
  data: () => ({
      search: '',
      headers: [
        {
          text: 'Subject',
          align: 'start',
          sortable: false,
          value: 'subject',
        },
        { text: 'Date', value: 'date'},
        { text: 'Sender', value: 'sender'},
        { text: 'Control #', value: 'control_no'},
        { text: 'PM Instruction', value: 'pm_instruction'},
        { text: 'Received By', value: 'received_by'},
        { text: 'Internal/External', value: 'type'},
        { text: 'Actions', value: 'actions', sortable: false },
      ],
      snackbar: false,
      logs: [],
      dialog: false,
      editLogs: [],
      editedId: null,
      editSnackbar: false,
      inputRules: [
            v => v.length >= 3 || 'Minimum length is 3 characters'
        ],
  }),
  methods: {
    getColor (type) {
      if (type == 'Internal') return '#039BE5'
      else if (type == 'External') return '#00897B'
      else return 'red'
    },
    del(doc) {
      if(confirm('Do you really want to delete this log?')) {

        db.collection('log').doc(doc).delete().then(function() {
          console.log("Document Successfully Deleted!");
        }).catch(function(err) {
          console.error("Error removing document: ", err);
        });
      }

      location.reload();
    },
    editBtn(item) {
      this.editLogs = item
      this.dialog = true
      this.editedId = item.id
    },
    updateLog() {
      db.collection('log').doc(this.editedId).update(this.editLogs)
      .then(() => {
        this.dialog = false,
        this.editSnackbar = true
      })
    }
  },
  created() {
    db.collection('log').onSnapshot(res => {
      const changes = res.docChanges();

      changes.forEach(change => {
        if (change.type === 'added'){
          this.logs.push({
            ...change.doc.data(),
            id: change.doc.id,
          })
        }
      })
    })
  }
}
</script>
