<template>
    <v-row justify="center">
        <v-dialog v-model="dialog" width="700" persistent>
            <template v-slot:activator="{ on, attrs }">
                <v-btn color="deep-purple darken-3 pa-5" dark v-bind="attrs" v-on="on" rounded>
                    <v-icon class="mx-1">mdi-file-plus</v-icon>
                    <span>Add Data</span>
                </v-btn>
            </template>
            <v-card>
                <v-card-title>
                    <span class="text-h5">Create Data</span>
                </v-card-title>
                <v-card-text>
                    <v-form ref="form">
                        <v-container>
                            <v-row>
                                <v-col sm="12">
                                    <v-textarea label="Subject" v-model="subject" prepend-icon="mdi-folder" outlined height="80" :rules="inputRules"></v-textarea>
                                </v-col>
                                <v-col sm="6">
                                    <v-menu ref="date1" v-model="date1" :close-on-content-click="false" transition="scale-transition" offset-y>
                                        <template v-slot:activator="{ on, attrs }">
                                            <v-text-field v-model="dateFormatted" label="Date" hint="MM/DD/YYYY format" persistent-hint prepend-icon="mdi-calendar" v-bind="attrs" @blur="date = parseDate(dateFormatted)" v-on="on" outlined :rules="inputRules"></v-text-field>
                                        </template>
                                        <v-date-picker v-model="date" no-title @input="date1 = false"></v-date-picker>
                                    </v-menu>
                                </v-col>
                                <v-col sm="6">
                                    <v-text-field label="Sender" v-model="sender" prepend-icon="mdi-account-arrow-right" outlined :rules="inputRules"></v-text-field>
                                </v-col>
                                <v-col sm="7">
                                    <v-text-field label="Control #" v-model="ctrlno" prepend-icon="mdi-numeric-9-box-multiple" outlined :rules="inputRules"></v-text-field>
                                </v-col>
                                <v-col sm="5">
                                    <v-select :items="types" v-model="type" label="Transmittal Type" prepend-icon="mdi-clipboard-file" outlined :rules="inputRules"></v-select>
                                </v-col>
                                <v-col sm="12">
                                    <v-textarea label="PM's Instruction" v-model="pmins" prepend-icon="mdi-file-powerpoint" outlined height="80" :rules="inputRules"></v-textarea>
                                </v-col>
                                <v-col sm="6">
                                    <v-select :items="items" label="Received By" v-model="received" prepend-icon="mdi-account-box-multiple" outlined :rules="inputRules"></v-select>
                                </v-col>
                                <v-col sm="6">
                                    <v-text-field v-model="redate" label="Received Date" hint="MM/DD/YYYY format" persistent-hint prepend-icon="mdi-calendar" outlined :rules="inputRules"></v-text-field>
                                </v-col>
                            </v-row>
                        </v-container>
                    </v-form>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn text @click="dialog = false" class="mx-0 mt-3">Cancel</v-btn>
                    <v-btn text @click="clear" class="mx-0 mt-3">Clear</v-btn>
                    <v-btn text class="mx-0 mt-3" @click="submit" :loading="loading" color="purple darken-1">Submit</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-row>
</template>

<script>
import db from '@/fb'

export default {
    data: vm => ({
        date: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
        dateFormatted: vm.formatDate((new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10)),
        dialog: false,
        subject: '',
        date1: false,
        redate: '',
        type: '',
        sender: '',
        ctrlno: '',
        pmins: '',
        received: '',
        items: ['Jen', 'Jane', 'Jan'],
        types: ['Internal', 'External'],
        inputRules: [
            v => v.length >= 3 || 'Minimum length is 3 characters'
        ],
        loading: false,
    }),
    computed: {
        computedDateFormatted() {
            return this.formatDate(this.date)
        }
    },
    watch: {
        date () {
        this.dateFormatted = this.formatDate(this.date)
      }
    },
    methods: {
        submit() {
            if (this.$refs.form.validate()){
                this.loading = true;

                const log = {
                    subject: this.subject,
                    date: this.date,
                    type: this.type,
                    sender: this.sender,
                    control_no: this.ctrlno,
                    pm_instruction: this.pmins,
                    received_by: this.received,
                    received_date: this.redate
                }

                db.collection('log').add(log).then(() => {
                    this.loading = false;
                    this.dialog = false;
                    this.$emit('logAdded');
                    this.subject = '',
                    this.date = '',
                    this.type = '',
                    this.sender = '',
                    this.ctrlno = '',
                    this.pmins = '',
                    this.received = '',
                    this.redate = ''
                    })
            }
        },
        clear() {
            this.subject = '',
            this.date = '',
            this.type = '',
            this.sender = '',
            this.ctrlno = '',
            this.pmins = '',
            this.received = '',
            this.redate = ''
        },
        formatDate (date) {
            if (!date) return null

            const [year, month, day] = date.split('-')
            return `${month}/${day}/${year}`
        },
        parseDate (date) {
            if (!date) return null

            const [month, day, year] = date.split('/')
            return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
        }
    }
}
</script>